/* Reset some default styles and set a base font size */
body,
h1,
h2,
p {
  margin: 0;
  padding: 0;
}

html {
  scroll-behavior: smooth;
}

body {
  font-family: Arial, sans-serif;
  background-color: #f4f4f4;
  min-height: 100vh; /* Minimum height to cover the viewport */
  margin: 0;
  padding: 0;
  overflow-x: hidden; /* Hide horizontal scrollbar */
}

/* Header styles */
header {
  background-color: #464646;
  color: #fff;
  padding: 1rem;
  text-align: center;
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  z-index: 1; /* Ensure header is above other elements */
}

header h1 {
  font-size: 2rem;
}

nav ul {
  list-style: none;
}

nav ul li {
  display: inline;
  margin-right: 1rem;
}

nav ul li:last-child {
  margin-right: 0;
}

nav a {
  color: #fff;
  text-decoration: none;
  font-weight: bold;
  transition: color 0.3s ease;
}

nav a:hover {
  color: #0056b3;
}

.content {
  margin-top: 6%;
  margin-left: 15%;
}

.sidebar {
  top: 9%;
  background-color: #333;
  color: #fff;
  padding: 15px;
  width: 200px;
  grid-area: sidebar;
  position: fixed;
  height: 100%;
}

.footer {
  grid-area: footer;
}

.on-this-page {
  grid-area: on-this-page;
  background-color: #fff;
  border: 1px solid #ddd;
  padding: 10px;
  max-height: 60%;
  overflow-y: auto;
  box-shadow: 0 0 5px rgba(0, 0, 0, 0.2);
  position: fixed;
  top: 12%; /* Adjust the top position as needed */
  right: 10px;
}

.content {
  grid-area: content;
}

/* Sidebar Title */
.sidebar-title {
  font-size: 1.5rem;
  margin-bottom: 20px;
  margin-left: 10px;
}

/* Sidebar List */
.sidebar-list {
  list-style: none;
  padding: 0;
}

/* Sidebar Item */
.sidebar-item {
  cursor: pointer;
  margin-bottom: 10px;
  margin-left: 15px; /* Add left margin to adjust the position */
}

/* Sidebar Link (Links and Other Items) */
.sidebar-link {
  text-decoration: none;
  display: flex;
  align-items: center;
  justify-content: space-between;
  color: #fff;
  transition: background-color 0.3s;
  padding: 5px 0;
}

/* Sidebar Link Text */
.sidebar-link-text {
  font-size: 1rem;
}
.sidebar li {
  margin-left: 10px;
}

/* Sidebar Icon (Arrow) */
.sidebar-icon {
  width: 20px;
  height: 20px;
  fill: #fff;
  transition: transform 0.3s;
}

/* Active Sublist */
.sublist.active {
  display: block;
  margin-left: 5px; /* Indent the sublist */
}

/* Sublist */
.sublist {
  list-style: none;
  display: none;
  padding-left: 0;
  margin-left: 0; /* Reset margin */

  /* Add a left border to sublist items */
}

.sublist li {
  margin-bottom: 2px;
  position: relative;
  padding-left: 15px; /* Adjust the distance of the line from the text */
}

/* Add a line before sublist items */
.sublist li::before {
  content: "";
  position: absolute;
  left: 0;
  top: 50%; /* Vertically center the line */
  transform: translateY(-50%);
  width: 5px; /* Width of the line */
  height: 2px; /* Height of the line */
  background-color: #fff; /* Color of the line */
}

/* Active Sidebar Item */
.sidebar-item.active {
  background-color: #555;
}

.content h1 {
  font-size: 1.5rem;
  margin-bottom: 1rem;
}

.content p {
  font-size: 1rem;
  line-height: 1.4;
}
.pageTitle {
  margin-left: 5%;
}

/* Footer styles */
footer {
  background-color: #464646;
  color: #fff;
  padding: 1rem;
  text-align: center;
  position: fixed;
  bottom: 0;
  left: 0;
  right: 0;
  z-index: 1; /* Ensure footer is above other elements */
}

/* On this Page Section */

.on-this-page h3 {
  font-size: 1.2rem;
  margin-top: 0;
}

.on-this-page ul {
  list-style: none;
  padding: 0;
}

.on-this-page li {
  margin-bottom: 5px;
}

.on-this-page a {
  text-decoration: none;
  color: #007bff;
}

.on-this-page a:hover {
  text-decoration: underline;
}
